//要获取远程数据的组件都要含有一个获取数据的函数 fetchDatas

export default function (defaultType = null) {
    return {
        data() {
            return{
                isLoading:true,
                data:defaultType
            }
        },
        async created() {
            this.data = await this.fetchDatas();
            this.isLoading = false
        }
    }
}