<template>
  <div class="blog-toc-container">
    <h2>目录</h2>
    <RightList :list="tocWithSelect" @select="handleSelect" />
  </div>
</template>

<script>
import RightList from "./RightLists.vue"
import  debounce  from "@/utils/debounce.js";
export default {
  components:{
    RightList
  },
  data() {
    return{
      avtiveAnchor:"",
      range:200,
    }
  },
 props:{
        toc:{
            type:Array,
        },

    },
  computed:{
      tocWithSelect() {
      const getTOC = (toc = []) => {
        return toc.map((t) => ({
          ...t,
          isSelect: t.anchor === this.avtiveAnchor,
          children: getTOC(t.children),
          }));
        };
      return getTOC(this.toc);
      },
      doms() {
      const doms = [];
      const addToDoms = (toc) => {
        for (const t of toc) {
          doms.push(document.getElementById(t.anchor));
          if (t.children && t.children.length) {
            addToDoms(t.children);
          }
        }
      };
      addToDoms(this.toc);
      return doms;
       },
    },
  created() {
      this.setSelectDebounce = debounce(this.setSelect, 50);
     this.$bus.$on("mainScroll",this.setSelectDebounce)
    },
  destroyed() {
 this.$bus.$off("mainScroll",this.setSelectDebounce)
    },
  methods:{
      handleSelect(item) {
        location.hash = item.anchor;
      },
      setSelect(scrollDom) {
        if(!scrollDom) {
          return
        }
        this.avtiveAnchor = "";
        for(let dom of this.doms) {
        if (!dom) {
          continue;
        }
          let top = dom.getBoundingClientRect().top;
        if(top >= 0 && top <= this.range) {
          this.avtiveAnchor = dom.id;
          return
          }else if(top < 0) {
             this.avtiveAnchor = dom.id;
            
          }else{
            return
          }
        }
      },
    }
}
</script>

<style lang='less' scoped>
.blog-toc-container {
  h2 {
    font-weight: bold;
    letter-spacing: 2px;
    font-size: 1em;
    margin: 0;
  }
}
</style>