<template>
  <div class="blog-comment-container">
    <MessageArea :list="data.rows"
     title="评论列表"
    :subTitle="`(${data.total})`"
     :isListLoading="isLoading"
     @submit="handleSubmit"
     />
  </div>
</template>

<script>
import fetchData from "@/fetchDate/fetchDate.js"
import MessageArea from "@/components/MessageArea"
import {getBlogComment,postComment} from '@/api/blog'
export default {
  mixins:[fetchData({total: 0, rows: []})],
  components:{
    MessageArea
  },
  data() {
    return {
      page: 1,
      limit: 10,
    };
  },
  computed:{
    hasMore() {
      if(this.data.rows.length < this.data.total){
        return true
      }
      else return false
    }
  },
    methods:{
    async  fetchDatas() {
        return await getBlogComment(this.$route.params.id,this.page,this.limit)
      },
     async handleSubmit(formData,callBack) {
       let result = await postComment({
         blogId:this.$route.params.id,
         ...formData
       });
       this.data.rows.unshift(result);
       this.data.total++;
        callBack("评论成功");
      },
     async getMoreComment() {
       if (!this.hasMore) {
         return
       }
       this.isLoading = true;
       this.page++;
       let result = this.data.rows;
       let resp =  await this.fetchDatas();
       this.data.total = resp.total;
       this.data.rows = result.concat(resp.rows);
       this.isLoading = false;
      },
      handleScroll(scrollItem) {
        if (this.isLoading || !scrollItem) {
        return;
      }
        let range = 100;
        let scrollHeight = scrollItem.scrollHeight;
        let scrolltop = scrollItem.scrollTop;
        let clientHeight = scrollItem.clientHeight;
        let des = Math.abs(scrolltop + clientHeight - scrollHeight)
        if (des <= range) {
          this.getMoreComment()
              
        }
      }
  },
      created() {
        this.$bus.$on("mainScroll",this.handleScroll)
      },
      destroyed() {
        this.$bus.$off("mainScroll", this.handleScroll);
  },
}
</script>

<style scoped lang="less">
.blog-comment-container {
  margin: 30px 0;
}
</style>