<template>
  <div class="message-area-container">
      <DataForm @submit="handleSubmit" />
      <h3>
          {{title}}
          <span>
              {{subTitle}}
          </span>
      </h3>
      <DataList :list="list"/>
      <div class="loading" v-loading="isListLoading"></div>
  </div>
</template>

<script>
import DataForm from'./DataForm.vue'
import DataList from './DataList.vue'

export default {
    components:{
        DataForm,
        DataList
    },
    props:{
        title:{
            type:String,
            default:""
        },
        subTitle:{
            type:String,
            default:""
        },
       isListLoading:{
            type:Boolean,
            default:false
        },
        list:{
            type:Array,
            default: () => []
        },

    },
    methods:{
        handleSubmit(formData,callBack){
             this.$emit('submit',formData,callBack)
        }
    }


}
</script>

<style scoped lang="less">
.loading {
  position: relative;
  height: 100px;
}
</style>